import MorphInner from './morph-inner-v1.vue'

export default {
  name: 'Morph',
  components: { morphInner: MorphInner },
  props: {
      lexemes: {
        type: Array,
        required: true
      },
      definitions: {
        type: Object,
        required: false,
        default: () => {}
      },
      linkedfeatures: {
        type: Array,
        required: false,
        default: () => []
      },
      translations: {
        type: Object,
        required: false,
        default: () => {}
      },
      morphDataReady: {
        type: Boolean,
        required: true
      }
  },
  methods: {
    showLexeme(lex) {
      return lex.isPopulated()
    },
    sendFeature (data) {
      this.$emit('sendfeature',data)
    },
  }
}
